import React, { useEffect, useRef } from 'react';
import './index.css'
import * as dat from 'dat.gui'
import io from 'socket.io-client'

// let host = '192.168.1.4'
// host = 'localhost'
// const socket = io(`${host}:4400`)

const server = "https://eng.med--lab.org";
const socket = io(server, { path: "/socket.io", transport: ["websocket"] });


function App() {
  const wrapper = useRef()
  const gui = new dat.GUI({ autoPlace: false, width: window.innerWidth > 360 ? 360 : window.innerWidth - 50, height: window.innerHeight })
  var parameters = {
    zoom: 1,
    accent: '#FF0000',
    main_layer: 0,
    other_layer: 0,
    another_layer: 0
  }

  const sendIt = (z) => {
    socket.emit('change_z', z)
  }

  const changeAccent = (c) => socket.emit('change_accent', c)


  const sendMainLayer = (r) => socket.emit('main_layer', r)
  const sendOtherLayer = (g) => socket.emit('other_layer', g)
  const sendAnotherLayer = (b) => socket.emit('another_layer', b)


  gui.add(parameters, 'zoom', 0, 1000).onChange(z => sendIt(z))

  gui.add(parameters, 'main_layer', 0, 1, .01).onChange(r => sendMainLayer(r))

  gui.add(parameters, 'other_layer', 0, 1, .01).onChange(g => sendOtherLayer(g))

  gui.add(parameters, 'another_layer', 0, 1, .01).onChange(b => sendAnotherLayer(b))

  gui.addColor(parameters, 'accent').onChange(c => changeAccent(c))


  useEffect(() => {
    wrapper.current.appendChild(gui.domElement);
    const li = document.getElementsByTagName('li')
    for (var i = 0; i < li.length; i++) li[i].style.height = '50px'

    const slider = document.getElementsByClassName('slider')
    for (var i = 0; i < slider.length; i++) slider[i].style.height = '50px'

    const input = document.getElementsByTagName('input')
    for (var i = 0; i < input.length; i++) input[i].style.height = '50px'

    document.querySelector('.close-button').innerHTML = ""

  })
  return (<div style={{ display: 'block', width: '90%', margin: 'auto' }}><div style={{ margin: 'auto' }} ref={wrapper}></div>
    <div style={{ fontFamily: 'sans-serif', fontWeight: 'bold', marginTop: '50px' }}>HELLO ~ this should allow you to control the visual :)</div>
  </div>
  );
}

export default App;
